@use "modules_scss/_colors.scss" as c;
@use "modules_scss/fonts" as t;

*,
*::before,
*::after {
  margin: 0;
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  overflow-x: hidden;
}
@media only screen and (max-width: 900px) {
  body {
    max-height: 100%;
    max-width: 100%;

    background-color: c.$background-color;
  }

  .main {
    height: 100vh;
    width: 100vw;
    display: flex;
    justify-content: center;
    flex-direction: column;

    &__first-switch-wrapper,
    .first-switch-wrapper {
      height: 100vh;
      width: 100vw;
      background-color: transparent;

      &__first-switch {
        height: 30vw;
        width: auto;
        margin: 10vw 40vw 0 15vw;
      }
      &__second-switch {
        height: 20vw;
        width: auto;
        margin: 20vw 30vw 0 15vw;
      }
      &__third-switch {
        height: 40vw;
        width: auto;
        margin-right: 10vw;
        margin-left: 55vw;
        margin-bottom: 2vw;
      }
    }
    &__throw-mechanics-wrapper,
    .throw-mechanics-wrapper {
      width: 100vw;
      height: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: transparent;

      &__directions,
      .directions {
        background-color: transparent;

        display: flex;
        justify-content: center;
        &__arrows,
        .arrows {
          background-color: transparent;
          margin-bottom: 30vw;
          position: relative;
          &__arrow {
            padding: 5vw;
          }
        }
      }
      &__snowball {
        height: 20vw;

        width: auto;
        background-color: transparent;
        animation: snowball-animation 1.2s ease-out 1;
        position: absolute;

        margin: 15vw 50vw 15vw 50vw;
        transform: translateX(-10vw);
        z-index: 100;
      }
    }
  }

  @keyframes snowball-animation {
    0% {
      // height: 20vw;
      margin: 15vw 50vw 15vw 50vw;
    }
    50% {
      height: 30vw;
      // margin: 100vw 50vw 15vw 50vw;
    }
    100% {
      // height: 20vw;
      margin: -300vw 50vw 15vw 50vw;
    }
  }
}

@media only screen and (min-width: 901px) and (max-width: 1200px) {
  body {
    max-height: 100%;
    max-width: 100%;

    background-color: c.$background-color;
  }

  .main {
    height: 100vh;
    width: 100vw;

    display: flex;
    justify-content: center;
    flex-direction: column;
    margin-top: 0;
    &__first-switch-wrapper,
    .first-switch-wrapper {
      width: 100vw;
      height: 40vw;

      background-color: transparent;
      margin: 0;

      &__first-switch {
        height: 10vw;
        width: auto;
        margin: 5vw 10vw 2vw 20vw;
      }
