    .throw-mechanics-wrapper {
      width: 100vw;
      height: auto;
      display: flex;
      flex-direction: column;
      justify-content: center;
      background-color: transparent;

      &__directions,
      .directions {
        width: 30vw;

        background-color: transparent;

        display: flex;
        justify-content: center;
        margin: 2vw 50vw 6vw 50vw;
        transform: translateX(-15vw);

        &__arrows,
