          background-color: transparent;

          &__arrow {
            padding: 2vw;
          }
        }
      }
      &__snowball {
        height: 10vw;
        margin: 2vw;
        padding: 0;
        width: auto;
        background-color: transparent;
        position: relative;
        // animation: snowball-animation 1.2s ease-out 1;
        position: absolute;

        margin: 15vw 50vw 5vw 50vw;
        transform: translateX(-5vw);
        z-index: 100;
      }
    }
    &__choose-power-wrapper,
    .choose-power-wrapper {
      width: 100vw;
      margin: 2vw 0 2vw 0;
      display: flex;

      justify-content: center;
      &__minus {
        width: 2vw;
        height: auto;
      }

      &__slider {
        margin: 5vw 2vw 5vw 2vw;
        -webkit-appearance: none; /* Override default CSS styles */
        width: 20vw;
        height: 0.5vw;
        border-radius: 50vw;
        background: c.$slider-color;
        outline: none;
        // opacity: 0.7;
        // -webkit-transition: 0.2s;
        // transition: opacity 0.2s;
      }
      // &__slider::-webkit-slider-ethumb {
      //   -webkit-appearance: none;
      //   appearance: none;
      //   width: 10vw;
      //   height: 10vw;
      //   border-radius: 50px;
      //   background: c.$snowman-color;
      //   cursor: pointer;
      // }
      &__slider::-moz-range-thumb {
        -webkit-appearance: none;
        width: 2vw;
        height: 2vw;
        border-radius: 5vw;
        background: c.$snowman-color;
        cursor: pointer;
      }
      &__plus {
        width: 2vw;
        height: auto;
      }
    }
  }

